<template>
  <div class="category_add">
    <breadcrumb path="Êñ∞Âª∫ÊñáÁ´†"></breadcrumb>

    <el-form :model="form" :rules="rules" style="width:80%;">
      <el-form-item label="Ê†áÈ¢ò" prop="title">
        <el-input v-model="form.title"></el-input>
      </el-form-item>
      <el-form-item label="ÊñáÁ´†Á±ªÂà´" prop="category">
        <el-select v-model="form.selected" placeholder="ÈÄâÊã©ÊñáÁ´†Á±ªÂà´">
          <el-option
            v-for="category in form.categories"
            :key="category._id"
            :label="category.name"
            :value="category._id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="ÁÆÄ‰ªã">
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 20}"
          placeholder="ËØ∑ËæìÂÖ•ÊñáÁ´†ÁÆÄ‰ªã"
          v-model="form.desc">
        </el-input>
      </el-form-item>
      <el-form-item label="ÂÜÖÂÆπ">
        <el-input
          type="textarea"
          :autosize="{ minRows: 25}"
          placeholder="ËØ∑ËæìÂÖ•ÂÜÖÂÆπ"
          v-model="form.content">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">Á´ãÂç≥ÂèëË°®</el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script type="es6">
  import breadcrumb from '../../components/breadcrumb.vue'
  export default {
    data() {
      return {
        form: {
          title: '',
          selected: '',
          categories: [],
          desc: '',
          content: '',
        },
        rules: {
          title: [
            {required: true, message: 'ËØ∑ËæìÂÖ•ÊñáÁ´†Ê†áÈ¢ò', trigger: 'blur'},
          ],
          category: [
            {required: true, message: 'ËØ∑ÈÄâÊã©ÊñáÁ´†ÂàÜÁ±ª', trigger: 'blur'}
          ],
        }
      }
    },
    created() {
      this.$http.get('/admin/content/add').then(response => {
        this.form.categories = response.data.categories;
        // console.log(response.data)
      }, response => {
        console.log('error:' + response);
      })
    },
    methods: {
      onSubmit() {
        if (this.form.selected == '' || this.form.title == '') {
          this.$message({
            message: 'Á±ªÂà´ÊàñÊ†áÈ¢ò‰∏çËÉΩ‰∏∫Á©∫',
            type: 'warning'
          });
          return;
        }
        this.$http.post('/admin/content/add', {
          category: this.form.selected,
          title: this.form.title,
          description: this.form.desc,
          content: this.form.content,
        }).then(response => {
          if (response.data.code == 0) {
            this.$message({
              message: 'ÊñáÁ´†‰øùÂ≠òÊàêÂäü,3ÁßíÂêé‰∏∫ÊÇ®Ë∑≥ËΩ¨Ëá≥ÊñáÁ´†ÁÆ°ÁêÜÈ¶ñÈ°µ',
              type: 'success',
            });
            var that = this;
            setTimeout(function () {
              that.$router.push({path: '/admin/content'})
            }, 3000);
          } else {
            this.$message({
              message: 'ÊúçÂä°Âô®ÂèëÁîüÊú™Áü•ÈîôËØØ',
              type: 'error'
            });
          }
        }, response => {
          console.log('error:' + response);
        })
      },
    },
    components: {
      breadcrumb,
    }
  }

</script>

